<template lang="pug">
  div.tabs
    ul.list
        li.item(v-for="tab in tabs")
            router-link(
                :to="tab.href"
                exact-active-class="active-tab"
            ).link {{tab.name}}
</template>

<script>
export default {
    data() {
        return {
            tabs: [
                {name: ' 1', href: "/"},
                {name: ' 2', href: "./works"},
                {name: ' 3', href: "./blog"}
            ]
        }
    }
}
</script>

<style src="../styles/components/tabs.scss" lang="scss" scoped>
</style>



